.singleTaskContainer {
  display: flex;
  width: 40%;
  border-radius: 5px;
  padding: 20px;
  margin-top: 15px;
  background-image: url("../media/single-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  align-items: center;
}

.text {
  flex: 1;
  padding: 5px;
  border: none;
  font-size: 20px;
  color: rgb(10, 10, 10);
}
.iconContainer {
  display: flex;
}
.icon {
  margin-left: 10px;
  font-size: 25px;
  cursor: pointer;
}

