.App {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-image: url("./media/bg-desktop.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; */
  background-color: rgb(18, 18, 18);
  font-family: cursive;
}

.heading {
  text-transform: uppercase;
  font-size: 40px;
  /* padding: 30px 0 15px 0; */
  color: white;
  z-index: 1;
  text-align: center;
  text-decoration: underline;
  /* border: 1px solid black; */
}

.figure {
  width: 80%;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgb(0, 0, 0);
  margin: 5px 0;
  text-align: center;
  border-radius: 15px;
  background-image: url("./media/forest.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.quote {
  /* margin: 0; */
  color: rgb(210, 217, 234);
  font-size: 20px;
}
.author {
  color: rgb(184, 184, 184);
  margin-bottom: 15px;
}
@media (max-width: 800px) {
  .heading {
    margin: 15px 0;
    font-size: 35px;
  }
}
